// "use client"

import styled from "styled-components";
import TitleSection from "@/components/newsDetail/TitleSection";
import { INewsDetail } from "@/models/newsDetail";
import { format } from "@/utils/dateFormat";
import Content from "@/components/newsDetail/Content";
import PopularNewsletter from "@/components/newsDetail/PopularNewsletter";
import NoContentsPage from "@/components/mypage/common(temporary)/NoContentsPage";

async function getLetter(letterId: string) {
  const res = await fetch(`http://localhost:3001/news/${letterId}`)
  const letter: INewsDetail = await res.json()
  return letter;
}


async function NewsletterDetailPage({
  params: {id},
}: {
  params: {id: string}
}) {
  const letter = await getLetter(id);
  // const [ newsInfo, setNewsInfo ] = useState<INewsDetail | null>(null)
  
  return (
    <div>
      <TitleSection
        category={letter.categoryId}
        title={letter.title}
        date={format(letter.createdAt)}
      />
      <div className="content-section">
        {letter.content ?
          <Content content={letter.content} />
          :
          <NoContentsPage text={"No Contents"} btnText={"메인으로"} />
        }
        <PopularNewsletter />
      </div>
    </div>
  );
}

export default NewsletterDetailPage;